.App {
  text-align: center;
  display: flex;
  height: 100vh;
  flex-direction: column;

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }

  // .content{
  //   flex: 1;
  // }
